import * as React from 'react';
import { Sidebar, Menu, Segment, Header, Button } from 'semantic-ui-react';

class SidebarComponent extends React.Component<any, any> {
  state = { visible: true };

  public handleItemClick = (e: any) => {
    console.log('handleItemClick', e);
  }

  public handleHideClick = () => this.setState({ visible: false })
  public handleShowClick = () => this.setState({ visible: true })
  public handleSidebarHide = () => this.setState({ visible: false })

  public render() {
    const { visible } = this.state;
    return (
      <div className="sidebar-container">
        <Button.Group>
          <Button disabled={visible} onClick={this.handleShowClick}>
            Show sidebar
          </Button>
          <Button disabled={!visible} onClick={this.handleHideClick}>
            Hide sidebar
          </Button>
        </Button.Group>
          <Sidebar.Pushable>
            <Sidebar
              as={Menu}
              animation='overlay'
              icon='labeled'
              onHide={this.handleSidebarHide}
              vertical
              visible={visible}
              width='thin'
            >
              <Menu.Item name='home' onClick={this.handleItemClick} />
              <Menu.Item
                name='messages'
                onClick={this.handleItemClick}
                />
              <Menu.Item
                name='friends'
                onClick={this.handleItemClick}
                />
            </Sidebar>
            <Sidebar.Pusher dimmed={visible}>
            <Segment basic>
              <Header as='h3'>Application Content</Header>
            </Segment>
          </Sidebar.Pusher>
          </Sidebar.Pushable>
      </div>
    );
  }
}

export default SidebarComponent;



